import segmentedControl from '@/components/uni-segmented-control/uni-segmented-control2';
export default {
	components: {
		segmentedControl
	},
	data() {
		return {
			items: ['服务', '优惠', '评价'],
			items2: ['洗车', '美容', '养护'],
			current: 0,
			current2: 0,
		};
	},
	methods: {
		/**
		 * 点击segmentedControl 事件回调
		 */
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
				console.log(this.current);
			}
		},
		onClickItem2(index) {
			if (this.current2 !== index) {
				this.current2 = index;
				console.log(this.current2);
			}
		}
	}
};
